@media screen and (max-width: 800px) {
  .container-padre_conocimientos {
    height: 338vw;
    /* background-repeat: no-repeat; */
    /* background-size: cover; */
    overflow: none !important;
    padding-top: 0vw !important;
  }

  .container-padre_conocimientos::before {
    content: "";
    position: absolute;
    left: 0;
    height: 348vw;
    /* background-image: url("../../img/fondoConocimientosMobile.jpg"); */
    /* background-attachment: unset; */
    background-position: center center !important;
    /* background-position-x: 23%; */
  }

  .style-h1-title {
    margin-top: 12%;
    font-size: 8vw;
  }

  .marco-separator-conocimientos,
  .marco-separator-inferior-conocimientos {
    width: 100%;
    height: 10vw;
    position: relative;
  }

  .marco-separator-conocimientos {
    margin-top: -2.5vw !important;
    bottom: 2vw;
    background-color: #5a5a5a;
    /* opacity: 0.2; */
  }

  .marco-separator-inferior-conocimientos {
    /* opacity: 0.2; */
    background-color: #2c2c2c;
    bottom: -5vw;
    top: 4vw !important;
  }

  /* .style-h3-subtitulo {
    font-size: 2.5vw;
  } */

  .column-general__tech {
    width: 84%;
    height: auto;
    max-width: 1250px;
    margin: auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 3%;
    align-items: start;
    /* background-color : red; */
  }

  .container__logo-title-tech {
    width: 40vw;
    height: 33vw;
    display: flex;
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;
    margin-top: 2%;
  }

  .container__logo-title-tech::before {
    width: 40vw;
    height: 33vw;
    opacity: 0.15;
  }

  .style-logo-tech {
    width: 17vw;
    height: 17vw;
  }

  .card__title {
    font-size: 25px;
    font-weight: 600;
  }

  .image-title-tech__container:hover .iconImage__tech {
    transform: scale(1.05);
  }

  .container__logo-title-tech {
    margin-top: 4vw;
  }

  .style-text__title-tech {
    font-size: 2.8vw;
    top: 2vw;
  }

  .image-title-tech__container:hover .card__title {
    text-shadow: 0.15vw 0.2vw 0.25vw #000000 !important;
    transition: all 0.5s !important;
  }
}

@media only screen and (min-width: 668px) and (max-width: 768px) {
  .style-text__title-tech {
    font-size: 1.5vw;
  }
}

@media only screen and (min-width: 768px) and (max-width: 768px) {
  .container-padre_conocimientos {
    padding-bottom: 0 !important;
  }

  .container-padre_conocimientos::before {
    margin-top: 10vw !important;
  }

  .marco-separator-conocimientos {
    margin-top: -11.5vw !important;
    top: 7vw;
  }

  .marco-separator-inferior-conocimientos {
    top: -2vw !important;
  }

  .container__logo-title-tech {
    margin-top: 6vw;
  }
}

@media only screen and (max-width: 320px) {
  .style-text__title-tech {
    font-size: 3vw;
  }
}

